import React, { useState } from "react";
import { HashRouter, Routes, Route } from "react-router-dom";

import { Phone, Clock, Instagram, Menu as MenuIcon } from "lucide-react";
import HeroSection from "./components/HeroSection";
import TrainersSection from "./components/TrainersSection";
import SubscriptionsSection from "./components/SubscriptionsSection";
import ScheduleSection from "./components/ScheduleSection";
import FacilitiesSection from "./components/FacilitiesSection";
import ContactSection from "./components/ContactSection";
import RegistrationModal from "./components/RegistrationModal";
import SideMenu from "./components/SideMenu";
import { Toaster } from "react-hot-toast";

const contactInfo = {
  phone: "+7 700 123 45 67",
  workingHours: {
    weekdays: "9:00 – 18:00",
  },
  instagram: "@unibaby",
};

const Header = ({ onMenuOpen }) => {
  return (
    <header className="fixed top-0 left-0 right-0 z-40 bg-white shadow-sm">
      <div className="container mx-auto px-4 py-4 flex items-center justify-between">
        <div className="flex items-center gap-4">
          <button
            onClick={onMenuOpen}
            className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <MenuIcon size={24} />
          </button>
          <div className="text-xl font-bold text-blue-600">UniBaby</div>
        </div>

        <div className="hidden md:flex items-center gap-6 text-sm text-gray-600">
          <div className="flex items-center gap-1">
            <Phone size={16} />
            <span>{contactInfo.phone}</span>
          </div>
          <div className="flex items-center gap-1">
            <Clock size={16} />
            <span>Пн-Пт: {contactInfo.workingHours.weekdays}</span>
          </div>
        </div>
      </div>
    </header>
  );
};

const Footer = () => {
  return (
    <div className="absolute bottom-0 left-0 right-0 p-6 border-t bg-gray-50">
      <div className="space-y-3 text-sm text-gray-600">
        <div className="flex items-center gap-2">
          <Phone size={16} />
          <span>{contactInfo.phone}</span>
        </div>
        <div className="flex items-center gap-2">
          <Clock size={16} />
          <span>Пн-Пт: {contactInfo.workingHours.weekdays}</span>
        </div>
        <div className="flex items-center gap-2">
          <Instagram size={16} />
          <span>{contactInfo.instagram}</span>
        </div>
      </div>
    </div>
  );
};

const Home = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isRegistrationOpen, setIsRegistrationOpen] = useState(false);
  const [selectedSubscription, setSelectedSubscription] = useState(null);

  const handleSubscriptionPayment = (subscription) => {
    setSelectedSubscription(subscription);
    setIsRegistrationOpen(true);
  };

  return (
    <div className="min-h-screen bg-white relative pb-32">
      <Header onMenuOpen={() => setIsMenuOpen(true)} />
      <SideMenu isOpen={isMenuOpen} onClose={() => setIsMenuOpen(false)} />

      <main className="pt-16">
        <HeroSection />
        <TrainersSection />
        <SubscriptionsSection onPayment={handleSubscriptionPayment} />
        <ScheduleSection />
        <FacilitiesSection />
        <ContactSection />
      </main>

      <RegistrationModal
        isOpen={isRegistrationOpen}
        onClose={() => setIsRegistrationOpen(false)}
        subscription={selectedSubscription}
      />

      <Footer />
      <Toaster />
    </div>
  );
};

function App() {
  return (
    <HashRouter>
      <Routes>
        <Route path="/" element={<Home />} />
      </Routes>
    </HashRouter>
  );
}

export default App;